import {AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, ViewChild} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTable} from '@angular/material/table';
import {BookingSearchTableDataSource} from './booking-search-table-data-source';
import {BookingSearchFiltersService} from '@pages/booking-search/booking-search-filters.service';
import {BookingTableItem} from '@pages/booking-search/booking-search-table/booking-table-item';
import {MatDialog} from '@angular/material/dialog';
import {ConfirmationDialogComponent} from '@pages/booking-search/confirmation-dialog/confirmation-dialog.component';
import {Router} from '@angular/router';
import {BreakpointObserver} from '@angular/cdk/layout';

@Component({
	selector: 'booking-search-table',
	templateUrl: './booking-search-table.component.html',
	styleUrls: ['./booking-search-table.component.scss']
})
export class BookingSearchTableComponent implements AfterViewInit, OnInit, OnChanges {
	@ViewChild(MatPaginator) paginator: MatPaginator;
	@ViewChild(MatSort) sort: MatSort;
	@ViewChild(MatTable) table: MatTable<BookingTableItem>;
	dataSource = new BookingSearchTableDataSource(this.bookingSearchFiltersService);
	@Input() filters;
	@Input() cityId;
	@Output() hasData = new EventEmitter();

	constructor(
		private bookingSearchFiltersService: BookingSearchFiltersService,
		private dialog: MatDialog,
		private router: Router,
		breakpointObserver: BreakpointObserver
	) {
		breakpointObserver.observe(['(max-width: 768px)']).subscribe((result) => {
			if (result.matches) {
				this.displayedColumns = ['floorNumber', 'placeName', /*'tagsS', */ 'booking'];
			} else {
				this.displayedColumns = ['floorNumber', 'placeName', 'tagsS', 'booking'];
			}
		});
	}

	/** Columns displayed in the table. Columns IDs can be added, removed, or reordered. */
	displayedColumns; /*= ['floorNumber', 'placeName', 'tagsS', 'booking'];*/

	ngOnInit() {}
	ngOnChanges() {
		this.hasData.emit(true);
		this.bookingSearchFiltersService.getBookingSearchTable(this.filters).subscribe((tableItems) => {
			this.dataSource.data = tableItems;
		});
	}
	getConfirmation(element) {
		const dialog = this.dialog.open(ConfirmationDialogComponent, {data: {element: element}});
		dialog.afterClosed().subscribe((result) => {
			if (result) {
				// TODO send data to server
				this.bookingSearchFiltersService.postReservation(element).subscribe((tableItems) => {
					this.dataSource.data = tableItems;
				});
				this.router.navigate(['home']);
			}
		});
	}

	ngAfterViewInit() {
		this.dataSource.sort = this.sort;
		this.dataSource.paginator = this.paginator;
		this.table.dataSource = this.dataSource;
	}
}
