import {Component, OnInit} from '@angular/core';
import {fabric} from 'fabric';
import {Rect} from 'fabric/fabric-impl';

@Component({
	selector: 'map-builder',
	templateUrl: './map-builder.component.html',
	styleUrls: ['./map-builder.component.scss']
})
export class MapBuilderComponent implements OnInit {
	canvas: any;
	private wall: Rect;
	private window: Rect;
	SVGSHE4KA: string;
	private trigger = false;
	constructor() {}

	ngOnInit(): void {
		this.canvas = new fabric.Canvas('canvas', {allowTouchScrolling: true});
		this.canvas.on('mouse:wheel', (opt) => {
			const delta = opt.e.deltaY;
			let zoom = this.canvas.getZoom();
			zoom *= 0.999 ** delta;
			if (zoom > 20) {
				zoom = 20;
			}
			if (zoom < 0.01) {
				zoom = 0.01;
			}
			this.canvas.zoomToPoint({x: opt.e.offsetX, y: opt.e.offsetY}, zoom);
			opt.e.preventDefault();
			opt.e.stopPropagation();
		});
		this.canvas.on('mouse:dblclick', (opt) => {
			opt.target.set('flipX', true);
			this.canvas.renderAll();
		});
		document.addEventListener('keydown', (e) => {
			console.log(e.code);
			if (e.code === 'Backspace') {
				if (this.canvas.getActiveObject()) {
					this.canvas.remove(this.canvas.getActiveObject());
				}
			}
			if (e.code === 'KeyR') {
				if (this.canvas.getActiveObject()) {
					this.canvas.getActiveObject().rotate(this.canvas.getActiveObject().get('angle') + 90);
				}
			}
			if (e.code === 'KeyF') {
				if (this.canvas.getActiveObject()) {
					this.canvas.getActiveObject().set('flipX', true);
					this.trigger = !this.trigger;
				}
			}
			this.canvas.renderAll();
		});
	}

	addMeetingRoom() {
		fabric.Image.fromURL('../assets/static_picture/mr.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	addSpecialPlace() {
		fabric.Image.fromURL('../assets/static_picture/SP.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	addWorkPlace() {
		fabric.Image.fromURL('../assets/static_picture/wp.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	addMap() {
		fabric.Image.fromURL('../assets/static_picture/office-plann.png', (img) => {
			img.setControlsVisibility({
				mt: false,
				mb: false,
				ml: false,
				mr: false
			});
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	addWindow() {
		this.window = new fabric.Rect({
			left: 80,
			top: 80,
			width: 50,
			height: 3,
			fill: 'blue'
		});
		this.window.setControlsVisibility({
			mt: false,
			mb: false,
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.window.lockScalingY = true;
		this.canvas.add(this.window);
	}

	addWall() {
		this.wall = new fabric.Rect({
			left: 80,
			top: 80,
			width: 100,
			height: 3,
			fill: 'grey'
		});
		this.wall.setControlsVisibility({
			mt: false,
			mb: false,
			bl: false,
			tl: false,
			br: false,
			tr: false
		});
		this.wall.lockScalingY = true;
		this.canvas.add(this.wall);
	}

	convert() {
		this.SVGSHE4KA = this.canvas.toSVG();
	}

	addPot() {
		fabric.Image.fromURL('../assets/static_picture/pot.png', (img) => {
			img.set({
				scaleX: 20 / img.width,
				scaleY: 20 / img.height
			});
			img.setControlsVisibility({
				mt: false,
				mb: false,
				bl: false,
				tl: false,
				br: false,
				tr: false,
				mr: false,
				ml: false
			});
			img.lockScalingX = true;
			img.lockScalingY = true;
			img.left = 80;
			img.top = 80;
			this.canvas.add(img);
		});
	}

	loadFromSVG() {
		const cnv = this.canvas;
		cnv.clear();
		fabric.loadSVGFromString(this.SVGSHE4KA, function(objects, options) {
			objects.forEach((obj) => {
				obj.lockMovementX = true;
				obj.lockMovementY = true;
				obj.setControlsVisibility({
					mt: false,
					mb: false,
					mr: false,
					ml: false,
					bl: false,
					tl: false,
					br: false,
					tr: false,
					mtr: false
				});
				cnv.add(obj).renderAll();
			});
		});
	}
}
